<template>
	<view>
		<web-view :src="webview_url" bindload="webviewOnLoad"  binderror="webviewOnError" bindmessage="onMessage" ></web-view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				webview_url:''
			}
		},
		webviewOnLoad:function(e){
			console.log(e)
		},
		onLoad:function(option){
			console.log(option)
			this.webview_url=option.ggg
			try{
				if(option.ggg=="true"){
					const bannerAd = tt.createRewardedVideoAd({
					  adUnitId: "3gmo9echhk53v76s7h"
					});
					  bannerAd.show().then(() => {
					      console.log("广告显示成功");
					    }).catch((err) => {
							bannerAd.load().then(() => {
							    console.log("手动加载成功");
							    return bannerAd.show();
							  });
					      console.log("广告组件出现问题", err);
					    });
					
					bannerAd.onClose((res) => {
					  if (!res.isEnded) {
					    uni.navigateBack({
					        delta: 9999
					    });
					  }
					});
				}
			}catch(e){}
			
			// console.log(61,this.gg)
			
		},
		methods: {
			
		}
	}
</script>

